import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { CustomSlideService } from './../../../custom-slide-service.service';

@Component({
  selector: 'app-male-female',
  templateUrl: './male-female.component.html',
  styleUrls: ['./male-female.component.css']
})
export class MaleFemaleComponent implements OnInit {
  gender:string;
  male:boolean;
  female:boolean;
  warning:boolean;

  @Output()
  next = new EventEmitter();
  @Output()
  prev = new EventEmitter();

  constructor(private service:CustomSlideService) { }

  ngOnInit() {
    if(this.service.slideData && this.service.slideData.gender=='male'){
      this.gender="male";
      this.male=true;
      this.female=false;
    }
    if(this.service.slideData && this.service.slideData.gender=="female"){
      this.gender="female";
      this.male=false;
      this.female=true;
    }
    this.warning = false;
  }

  handlemaleclick(){
    console.log(":: male clicked");
    this.gender="male";
    this.male=true;
    this.female=false;
    //moving the slider & updating the vals in service
    this.service.updateGender(this.gender);
    setTimeout(() => {
      this.next.emit();
      console.log(":: emitted from child");
    }, 500);

  }
  handlefemaleclick(){
    console.log(":: female clicked");
    this.gender="female";
    this.male = false;
    this.female = true;
    //moving the slider & updating the vals in service
    this.service.updateGender(this.gender);
    setTimeout(() => {
      this.next.emit();
      console.log(":: emitted from child");
    }, 500);
  }

}
