import { Component, NgZone, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { CustomSlideService } from 'src/app/custom-slide-service.service';
import { Router, ActivatedRoute } from '@angular/router';
import { CheckoutService } from 'src/app/checkout.service';
import { environment } from '../../../environments/environment';
import { RzpOrder, PaymentCaptureInfo } from 'src/app/interfaces/razorpay';
import { WindowService } from 'src/app/window.service';
import { BehaviorSubject } from 'rxjs';
import { MatDialog } from '@angular/material';
import { PaymentSuccessComponent } from '../payment-success/payment-success.component';

@Component({
  selector: 'app-checkout',
  templateUrl: './checkout.component.html',
  styleUrls: ['./checkout.component.css']
})
export class CheckoutComponent implements OnInit {

  price: number = 0;
  checkoutform: FormGroup;
  formError: boolean;


  success = new BehaviorSubject<boolean>(false);

  constructor(private fb: FormBuilder, private slideService: CustomSlideService,
    private router: Router, private checkoutService: CheckoutService, private zone: NgZone,
    private windowService: WindowService, private route: ActivatedRoute, private CustomSlideService: CustomSlideService) {

    this.checkoutform = this.fb.group({
      name:['',Validators.required],
      email:['', Validators.required],
      phone:['', Validators.required]
    })
   }

  ngOnInit() {
    if(!this.CustomSlideService.allValuesPresent()) {
      this.router.navigateByUrl('/');
    } else if(this.checkoutService.getPricing()) {
      this.price = this.checkoutService.getPricing();
    }
  }

  proceed() {
    // check for form filled
    const name = this.checkoutform.controls.name.value;
    const email = this.checkoutform.controls.email.value;
    const contact = this.checkoutform.controls.phone.value;
    if(!this.checkoutform.valid) {
      this.formError = true;
    } else {
      this.formError = false;
      this.CustomSlideService.updateNameEmailContact(name, email, contact);

      console.log(":: slide data", this.CustomSlideService.slideData);

      // prepare razorpay config
      let razorPayOptions = {
        "key": "rzp_test_d8qs8mxXhWH9vS",
        "amount": this.price,
        "currency": environment.razorpay.currency,
        "name": environment.razorpay.name,
        "description": "Personalized Diet Plan",
        "order_id": "",
        "image": "https://picsum.photos/200",
        "theme": {
          "color": environment.razorpay.color
        },
        "prefill": {
          "name": this.checkoutform.get('name').value,
          "email": this.checkoutform.get('email').value,
          "contact": this.checkoutform.get('phone').value
        },
        "handler": function(res){
          console.log(':: first captur handler', res);
        }
      }
      // Overriding handler fxn
      razorPayOptions.handler = (this.handler).bind(this);
      // placeOrder on Server
      console.log(':: proceed button clicked');
      const slideData = this.CustomSlideService.slideData;
      console.log(":: slide data checkout", slideData);
      this.checkoutService.placePersonalizedOrder(slideData).subscribe(
        (order: RzpOrder) => {
          console.log(':: order from server ', order);
          this.checkoutService.setOrderId(order.rzp_order_id);
          // here open razorpay dialog
          const orderId = order.id;
          razorPayOptions.order_id = orderId;
          var rzp = new this.windowService.window.Razorpay(razorPayOptions);
          rzp.open();
        },
        (err: Error) => {
          this.router.navigate[('payment-failed')];
        });
    }
  }

  handleCancel(){
    this.router.navigateByUrl('/');
  }

  handler(response) {
    console.log(':: razorpay response after payment done', response);
    const rzp_order_id = this.checkoutService.getOrderId();
    this.checkoutService.capturePayment({ ...response, amount: this.price, rzp_order_id: rzp_order_id}).subscribe(
      (res:PaymentCaptureInfo)=>{
        console.log(':: captured payment successfuly', res);
        if(res.error_code==null){
          this.zone.run(() => {
            this.router.navigate(['/login']);
          });

        }
        else{
          this.zone.run(() => {
            this.router.navigate[('payment-failed')];
          });
        }
        // this.windowService.window.location.href="/payment-success";
      },
      (err: Error) => {
        this.zone.run(() => {
          this.router.navigate[('payment-failed')];
        });
        //this.windowService.window.location.href="/payment-failed";
      }
    );
  }

}
