import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { parse } from 'querystring';
import { CheckoutService } from 'src/app/checkout.service';

@Component({
  selector: 'app-diet-plan',
  templateUrl: './diet-plan.component.html',
  styleUrls: ['./diet-plan.component.css']
})
export class DietPlanComponent implements OnInit {

  weekOptions: string[] = ['1', '2', '3', '4', '5'];
  weekdayOptions: string[] = ['M', 'T', 'W', 'Th', 'F', 'S', 'Su'];
  weekOneDays: string[] = ['1', '2', '3', '4', '5', '6', '7'];
  weekTwoDays: string[] = ['8', '9', '10', '11', '12', '13', '14'];
  weekThreeDays: string[] = ['15', '16', '17', '18', '19', '20', '21'];
  weekFourDays: string[] = ['22', '23', '24', '25', '26', '27', '28'];
  weekFiveDays: string[] = ['28', '30','31'];
  selectedWeek: string = this.weekOptions[0];
  selectedWeekday:string = this.weekOneDays[0];
  selectedDayData: any[] = [];
  dietplan:any;

  constructor(private service: CheckoutService, private router: Router) {
  }

  ngOnInit() {
    this.dietplan = this.service.getDietPlan();
    if(this.dietplan) {
      this.selectedDayData = this.dietplan.data.filter((data: any) => data.day === 1);
      console.log(":: selected diet Plan", this.selectedDayData);
    } else {
      this.router.navigate(['login']);
    }
    

  }

  weekNext(){
    const index = this.weekOptions.findIndex((week: string) => week === this.selectedWeek );
    this.selectedWeek = this.weekOptions[index+1];
    this.getSelectedWeekOptions(this.selectedWeek);
    this.getSelectedDayData(parseInt(this.selectedWeekday));
  }

  weekPrev(){
    const index = this.weekOptions.findIndex((week: string) => week === this.selectedWeek );
    this.selectedWeek = this.weekOptions[index-1];
    this.getSelectedWeekOptions(this.selectedWeek);
    this.getSelectedDayData(parseInt(this.selectedWeekday));
  }

  weekdaySelected(day: string) {
    this.selectedWeekday = day;
    const dayTrimmed = day.split(' ')[1]? parseInt(day.split(' ')[1]) : parseInt(day);
    this.getSelectedDayData(dayTrimmed);

  }

  getSelectedWeekOptions(week: string) {
    switch (week) {
      case '1': this.selectedWeekday = this.weekOneDays[0];
        break;
      case '2': this.selectedWeekday = this.weekTwoDays[0];
        break;
      case '3': this.selectedWeekday = this.weekThreeDays[0];
        break;
      case '4' : this.selectedWeekday = this.weekFourDays[0];
        break;
      case '5': this.selectedWeekday = this.weekFiveDays[0];
      default: ''
        break;
    }
  }

  getSelectedDayData(day: number) {
    console.log(":: day is", day);
    this.selectedDayData = this.dietplan.data.filter((data: any) => data.day === day);
    console.log(":: selectedDay Data", this.selectedDayData);
  }

}
