import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { CustomSlideService } from 'src/app/custom-slide-service.service';

@Component({
  selector: 'app-typical-day',
  templateUrl: './typical-day.component.html',
  styleUrls: ['./typical-day.component.css']
})
export class TypicalDayComponent implements OnInit {

  @Output()
  next = new EventEmitter;

  @Output()
  prev = new EventEmitter;

  warning:boolean;



  atoffice:boolean = false;
  gooutregular:boolean = false;
  onfoot:boolean= false;
  labour:boolean= false;
  athome:boolean= false;

  constructor(private fb: FormBuilder, private service:CustomSlideService) { }

  ngOnInit() {
    if(this.service.slideData.typical_day == "atoffice"){
      this.atoffice=true;
    }
    if(this.service.slideData.typical_day == "gooutregular"){
      this.gooutregular=true;
    }
    if(this.service.slideData.typical_day == "onfoot"){
      this.onfoot=true;
    }
    if(this.service.slideData.typical_day == "labour"){
      this.labour=true;
    }
    if(this.service.slideData.typical_day == "athome"){
      this.athome=true;
    }
  }

  handleAtOffice(){
   this.service.updateTypicalDay("atoffice");
   this.atoffice = true;
   setTimeout(() => {
    this.next.emit();
    console.log(":: emitted from child");
  }, 500);
  }

  handleGoOutRegular(){
    this.service.updateTypicalDay("gooutregular");
    this.gooutregular = true;
    setTimeout(() => {
      this.next.emit();
      console.log(":: emitted from child");
    }, 500);
  }
  handleOnFoot(){
   this.service.updateTypicalDay("onfoot");
   this.onfoot = true;
   setTimeout(() => {
    this.next.emit();
    console.log(":: emitted from child");
  }, 500);
  }

  handleLabour(){
    this.service.updateTypicalDay("labour");
    this.labour = true;
    setTimeout(() => {
      this.next.emit();
      console.log(":: emitted from child");
    }, 500);
  }

  handleAtHome(){
    this.athome = true;
    this.service.updateTypicalDay("athome");
    setTimeout(() => {
      this.next.emit();
      console.log(":: emitted from child");
    }, 500);
  }

  handlePrev(){
    //updating slide
    this.prev.emit();

  }

}
