import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ContactUsComponent } from './pages/contact-us/contact-us.component';
import { PageNotFoundComponent } from './pages/page-not-found/page-not-found.component';
import { PersonalizedDietComponent } from './pages/personalized-diet/personalized-diet.component';
import { ProfileSummaryComponent } from './pages/profile-summary/profile-summary.component';
import { CheckoutComponent } from './pages/checkout/checkout.component';
import { DietPlanComponent } from './components/diet-plan/diet-plan.component';
import { PaymentSuccessComponent } from './pages/payment-success/payment-success.component';
import { PaymentFailureComponent } from './pages/payment-failure/payment-failure.component';
import { LoginComponent } from './pages/login/login.component';


const routes: Routes = [
  { path: '', component: PersonalizedDietComponent },
  { path: 'contact-us', component: ContactUsComponent },
  { path: 'profile-summary', component: ProfileSummaryComponent },
  { path: 'checkout', component: CheckoutComponent  },
  { path: 'diet-plan', component: DietPlanComponent },
  { path: 'payment-success', component: PaymentSuccessComponent },
  { path: 'payment-failed', component: PaymentFailureComponent },
  { path: 'login', component: LoginComponent },
  { path: '**', component: PageNotFoundComponent},
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {scrollPositionRestoration: 'enabled'})],
  exports: [RouterModule]
})
export class AppRoutingModule { }
