import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { CheckoutService } from 'src/app/checkout.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  
  loginForm: FormGroup;
  apiError: boolean;

  constructor(private fb: FormBuilder, private router: Router, private service: CheckoutService) { 
    this.loginForm = this.fb.group({
      email:['', Validators.required],
    });
  }

  ngOnInit() {
    this.apiError = false;
  }

  login(email: string) {
    console.log(":: login", email);
    if(this.loginForm.valid) {
    this.service.getDietPlanFromServer(email).subscribe((res: any) =>  {
      if(res) {
        this.service.setDietPlan(res);
        this.router.navigateByUrl('/diet-plan');
      } else {
        this.apiError = true;
      }
      
    });
    }
  }

  handleCancel() {
    console.log(":: cancel login")
    this.router.navigateByUrl('/');
  }

}
