import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { CustomSlideService } from 'src/app/custom-slide-service.service';

@Component({
  selector: 'app-water-intake',
  templateUrl: './water-intake.component.html',
  styleUrls: ['./water-intake.component.css']
})
export class WaterIntakeComponent implements OnInit {

  @Output() next = new EventEmitter;
  @Output() prev = new EventEmitter;
  water:string;
  warning:boolean;
  morethanthreelitre:boolean;
  onelitre:boolean;
  twolitre:boolean;

  constructor(private service:CustomSlideService) { }

  ngOnInit() {
    if(this.service.slideData.water_intake == "onelitre"){
      this.onelitre=true;
    }
    if(this.service.slideData.water_intake == "twolitre"){
      this.twolitre=true;
    }
    if(this.service.slideData.water_intake == "morethanthreelitre"){
      this.morethanthreelitre=true;
    }

  }

  handleonelitre(){
    this.onelitre = true;
    this.water="onelitre";
    this.service.updateWaterIntake(this.water);
    setTimeout(() => {
      this.next.emit();
      console.log(":: emitted from child");
    }, 500);
  }

  handleoneandhalflitre(){
    this.water="onepointfivelitre";
    this.service.updateWaterIntake(this.water);
    this.next.emit();
  }

  handletwolitre(){
    this.twolitre = true;
    this.water="twolitre";
    this.service.updateWaterIntake(this.water);
    setTimeout(() => {
      this.next.emit();
      console.log(":: emitted from child");
    }, 500);
  }

  handlethreelitre(){
    this.water="threelitre";
    this.service.updateWaterIntake(this.water);
    this.next.emit();
  }

  handlemorethanthreelitre(){
    this.morethanthreelitre = true;
    this.water="morethanthreelitre";
    this.service.updateWaterIntake(this.water);
    setTimeout(() => {
      this.next.emit();
      console.log(":: emitted from child");
    }, 500);
  }

  handlePrev(){
    this.prev.emit();

  }

}
