import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { FormBuilder } from '@angular/forms';
import { CustomSlideService } from 'src/app/custom-slide-service.service';
import { Router } from '@angular/router';
import { CheckoutService } from 'src/app/checkout.service';


@Component({
  selector: 'app-true',
  templateUrl: './true.component.html',
  styleUrls: ['./true.component.css']
})
export class TrueComponent implements OnInit {

  @Output()
  next = new EventEmitter;
  @Output()
  prev = new EventEmitter;
  warning: boolean;
  sleep: boolean = false;
  eatlate: boolean = false;
  salt: boolean = false;
  sweets: boolean = false;
  none: boolean = false;

  constructor(private fb: FormBuilder, private service: CustomSlideService, private router: Router, private CheckoutService: CheckoutService) { }

  ngOnInit() {

  }

  handleSleep() {
    this.none = false;
    this.sleep = !this.sleep;
  }

  handleEatLate() {
    this.none = false;
    this.eatlate = !this.eatlate;

  }
  handleSalt() {
    this.none = false;
    this.salt = !this.salt;

  }

  handleSweets() {
    this.none = false;
    this.sweets = !this.sweets;

  }

  handleNone() {
    this.none = !this.none;
    if (this.none == true) {
      this.sleep = false;
      this.eatlate = false;
      this.salt = false;
      this.sweets = false;
    }
  }

  handlePrev() {
    this.prev.emit();
  }

  handleNext() {
    var trueStatement = [];
    if (this.sleep) {
      trueStatement.push("I do not get Enough Sleep");
    }
    if (this.salt) {
      trueStatement.push("I consume a lot of salt");
    }
    if (this.sweets) {
      trueStatement.push("I cannot give up eating sweets");
    }
    if (this.eatlate) {
      trueStatement.push("I eat late at Night");
    }
    if (this.none) {
      trueStatement.push("None of the above");
    }
    //checking the length of the array
    if (trueStatement.length >= 1) {
      this.service.updateTrue(trueStatement);
      this.CheckoutService.getPersonalizedDietPrice().subscribe((amount: number) => {
        console.log(":: pricing for plan", amount);
        const actualAmount = 100*amount;
        this.CheckoutService.setPricing(actualAmount);
        this.router.navigate(['profile-summary']);
      });
    }
    else {
      this.warning = true;
    }
  }
}
