import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { WindowService } from './window.service';
import { catchError, map } from 'rxjs/operators';
import { throwError, Observable } from 'rxjs';
import { RzpOrder, PaymentCaptureInfo } from './interfaces/razorpay';
import { PersonalizedSlideData } from './interfaces/personalized-slide-data';
import { environment as env, environment} from '../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class CheckoutService {

  private pricing: number;
  headers = new HttpHeaders().set('x-api-key', '70da442b-ac6e-4c02-a429-e16132c4fc58')
  rzp_order_id: string;
  dietPlan: any;


  constructor(private http: HttpClient) { }

  /**
   * send order related data to server, to create order in backend
   * returns order object if everything goes right, returns error if we get error
   */
  placePersonalizedOrder(data: PersonalizedSlideData): Observable<RzpOrder> {
    const mockData = {
      ...data,
      true_statements: data.true_statements.join()
    }
    // const url = `${env.API_URL}/orders/create-personalized-order`;
    const localUrl = `${env.API_URL}create?model=order`;
    return this.http.post<RzpOrder>(localUrl, mockData, {headers: this.headers})
    .pipe(
      catchError((err) => {
        return throwError(err);
      })
    );
  }

  getDietPlanFromServer(email: string) {
    return this.http.get(`${env.API_URL}/get-dietplan?email=${email}`, {headers: this.headers});
  }

  getPersonalizedDietPrice(): Observable<number> {
    // const url = 'http://95.111.199.28/plans/pricing';
    const localUrl = `${env.API_URL}diet-plan-pricing`;
    return this.http.get(localUrl, {headers: this.headers})
    .pipe(
      map((res) => (res as any).amount),
      catchError((err: Error) => throwError(err))
    );
  }

  capturePayment(details: any){
    return this.http.post<PaymentCaptureInfo>(`${environment.API_URL}capture-payment`, details, {headers: this.headers}).pipe(
      catchError((err) => throwError(err))
    );
  }

  setPricing(price: number) {
    this.pricing = price;
  }

  getPricing() {
    return this.pricing;
  }

  setOrderId(rzp_order_id: string) {
    this.rzp_order_id = rzp_order_id;
  }

  setDietPlan(dietPlan: any) {
    this.dietPlan = dietPlan;
  }

  getDietPlan() {
    return this.dietPlan;
  }

  getOrderId() {
    return this.rzp_order_id;
  }

}
