import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { CustomSlideService } from 'src/app/custom-slide-service.service';

@Component({
  selector: 'app-foodpreference',
  templateUrl: './foodpreference.component.html',
  styleUrls: ['./foodpreference.component.css']
})
export class FoodpreferenceComponent implements OnInit {

  @Output() next = new EventEmitter;
  @Output() prev = new EventEmitter;

  foodpref:string;
  veg:boolean;
  nonveg:boolean;
  egg:boolean;
  warning:boolean;

  constructor(private service:CustomSlideService) { }

  ngOnInit() {
   if(this.service.slideData.food_pref=="veg"){
     this.foodpref = this.service.slideData.food_pref;
     this.veg = true;
     this.nonveg = false;
     this.egg = false;
   }

   if(this.service.slideData.food_pref=="non_veg"){
    this.foodpref = this.service.slideData.food_pref;
    this.veg = false;
    this.nonveg = true;
    this.egg = false;
  }

  if(this.service.slideData.food_pref=="egg"){
    this.foodpref = this.service.slideData.food_pref;
    this.veg = false;
    this.nonveg = false;
    this.egg = true;
  }
  this.warning = false;

}

  handleveg(){
    console.log(":: veg clicked");
    this.foodpref="veg";
    this.veg=true;
    this.nonveg=false;
    this.egg=false;
    this.service.updateFoodPref(this.foodpref);
      //updating slide
      setTimeout(() => {
        this.next.emit();
        console.log(":: emitted from child");
      }, 500);
  }


  handlenonveg(){
    console.log(":: nonveg clicked");
    this.foodpref="non_veg";
    this.veg=false;
    this.nonveg=true;
    this.egg=false;
    this.service.updateFoodPref(this.foodpref);
      //updating slide
      setTimeout(() => {
        this.next.emit();
        console.log(":: emitted from child");
      }, 500);
  }


  handleegg(){
    console.log(":: egg clicked");
    this.foodpref="egg";
    this.veg=false;
    this.nonveg=false;
    this.egg=true;
    this.service.updateFoodPref(this.foodpref);
      //updating slide
      setTimeout(() => {
        this.next.emit();
        console.log(":: emitted from child");
      }, 500);
  }

  handlePrev(){
     //updating property in service

      this.service.updateFoodPref(this.foodpref);
      //updating slide
      this.prev.emit();
  }
}
