import { Component, OnInit, Input } from '@angular/core';
import { CustomSlideService } from './../../custom-slide-service.service';

@Component({
  selector: 'app-personalized-diet',
  templateUrl: './personalized-diet.component.html',
  styleUrls: ['./personalized-diet.component.css']
})
export class PersonalizedDietComponent implements OnInit {

  selectedSlide:number;



  constructor(private service:CustomSlideService) { }

  ngOnInit() {
    this.selectedSlide=0;
    this.service.resetData();
  }

  handleNextInParent(){
    this.selectedSlide+=1;
  }

  handlePrevInParent(){
    this.selectedSlide-=1;
  }

}
