import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { CustomSlideService } from 'src/app/custom-slide-service.service';

@Component({
  selector: 'app-goal',
  templateUrl: './goal.component.html',
  styleUrls: ['./goal.component.css']
})
export class GoalComponent implements OnInit {

  @Output()
  next = new EventEmitter;

  @Output()
  prev = new EventEmitter;

  goal:string;
  warning:boolean;

  constructor(private service:CustomSlideService) { }

  ngOnInit() {
    // initailsing the values in service
    this.goal = this.service.slideData.goal;
  }


  handleweightgain(){
    console.log(":: weight gain selected");
    this.goal = "weight_gain";
      this.service.updateGoal(this.goal);
      // changing slide
      setTimeout(() => {
        this.next.emit();
        console.log(":: emitted from child");
      }, 500);
  }

  handleweightloss(){
    this.goal = "weight_loss";
    this.service.updateGoal(this.goal);
      // changing slide
      setTimeout(() => {
        this.next.emit();
      }, 500);
  }

  handlemusclebuilding(){
    this.goal = "muscle_building";
    this.service.updateGoal(this.goal);
      // changing slide
      setTimeout(() => {
        this.next.emit();
        console.log(":: emitted from child");
      }, 500);
  }

  handlePrev(){

      //updating property in service
        this.service.updateGoal(this.goal);
      //changing slide
      this.prev.emit();
  }
}
