import { Component, OnInit, EventEmitter, Output } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { CustomSlideService } from 'src/app/custom-slide-service.service';

@Component({
  selector: 'app-physical-active',
  templateUrl: './physical-active.component.html',
  styleUrls: ['./physical-active.component.css']
})
export class PhysicalActiveComponent implements OnInit {

  @Output()
  next = new EventEmitter;

  @Output()
  prev = new EventEmitter;

  warning:boolean;



  noactivity:boolean = false;
  oftenWalk:boolean = false;
  oneTwoTimes:boolean= false;
  threeFiveTimes:boolean= false;
  fiveSevenTimes:boolean= false;

  constructor(private fb: FormBuilder, private service:CustomSlideService) { }

  ngOnInit() {
    if(this.service.slideData.physical_activity == "noactivity"){
      this.noactivity=true;
    }
    if(this.service.slideData.physical_activity == "oftenWalk"){
      this.oftenWalk=true;
    }
    if(this.service.slideData.physical_activity == "1-2 times"){
      this.oneTwoTimes=true;
    }
    if(this.service.slideData.physical_activity == "3-5 times"){
      this.threeFiveTimes=true;
    }
    if(this.service.slideData.physical_activity == "5-7 times"){
      this.fiveSevenTimes=true;
    }
  }

  handlenoactivity(){
   this.service.updatePhysicalActive("noactivity");
   this.noactivity = true;
   setTimeout(() => {
      this.next.emit();
      console.log(":: emitted from child");
    }, 500);
  }

  handleoften(){
    this.service.updatePhysicalActive("oftenWalk");
    this.oftenWalk = true;
    setTimeout(() => {
      this.next.emit();
      console.log(":: emitted from child");
    }, 500);
  }
  handleonetwo(){
   this.service.updatePhysicalActive("1-2 times");
   this.oneTwoTimes = true;
   setTimeout(() => {
      this.next.emit();
      console.log(":: emitted from child");
    }, 500);
  }

  handlethreefive(){
    this.service.updatePhysicalActive("3-5 times");
    this.threeFiveTimes = true;
    setTimeout(() => {
      this.next.emit();
      console.log(":: emitted from child");
    }, 500);
  }

  handlefiveseven(){
    this.service.updatePhysicalActive("5-7 times");
    this.fiveSevenTimes = true;
    setTimeout(() => {
      this.next.emit();
      console.log(":: emitted from child");
    }, 500);
  }

  handlePrev(){
    //updating slide
    this.prev.emit();

  }

}
