import { Injectable } from '@angular/core';
import { PersonalizedSlideData } from './interfaces/personalized-slide-data';

@Injectable()
export class CustomSlideService {

  slideData: PersonalizedSlideData = {
    gender: undefined,
    name: '',
    email: '',
    age: undefined,
    height: undefined,
    weight: undefined,
    physical_activity: undefined,
    goal: undefined,
    food_pref: undefined,
    water_intake: undefined,
    typical_day: undefined,
    true_statements: undefined,
    rzp_order_id: '',
    contact: ''
  };

  constructor() { }

  updateGender(gender:string){
    this.slideData.gender = gender;
  }

  resetData() {
    this.slideData = {
      gender: undefined,
      name: '',
      email: '',
      age: undefined,
      height: undefined,
      weight: undefined,
      physical_activity: undefined,
      goal: undefined,
      food_pref: undefined,
      water_intake: undefined,
      typical_day: undefined,
      true_statements: undefined,
      rzp_order_id: '',
      contact: ''
    };
  }

  updateMeasurement(age:number,height: string,weight:number){
    this.slideData.age = age;
    this.slideData.height = height;
    this.slideData.weight = weight;
  }

  updatePhysicalActive(physicalActive:string){
    this.slideData.physical_activity = physicalActive;
  }

  updateGoal(goal:string){
    this.slideData.goal = goal;
  }

  updateFoodPref(foodpref:string){
    this.slideData.food_pref = foodpref;
  }

  updateWaterIntake(waterintake:string){
    this.slideData.water_intake = waterintake;
  }

  updateTypicalDay(typicalDay:string){
    this.slideData.typical_day = typicalDay;
  }

  updateTrue(truth:string[]){
    this.slideData.true_statements = truth;
  }

  updateNameEmailContact(name: string, email: string, contact: string) {
    this.slideData.name = name;
    this.slideData.email = email;
    this.slideData.contact = contact;
  }

  allValuesPresent():boolean{
    for(var key in this.slideData){
      if(this.slideData[key] === undefined){
        return false;
      }
    }
    return true;
  }

}
