import { Component, OnInit } from '@angular/core';
import { NgxChartsModule } from '@swimlane/ngx-charts';
import { Router } from '@angular/router';
import { CustomSlideService } from './../../custom-slide-service.service';
import { CheckoutService } from 'src/app/checkout.service';

@Component({
  selector: 'app-profile-summary',
  templateUrl: './profile-summary.component.html',
  styleUrls: ['./profile-summary.component.css']
})
export class ProfileSummaryComponent implements OnInit {

  view: any[] = [330, 350];
  pricing: number;
  legend: boolean = false;
  legendPosition: string = 'below';

  // proerties for display BMI
  BMI: number = 0;
  userHeightM: number;
  userWeightKg: number;
  gender: string;
  userAge: any;
  idealWeight: number;
  calorie: number;

  single: any[];
  customColors: any;

  singleCalorie: any[];

  colorScheme = {
    domain: ['orchid']
  };

  colorSchemeCalorie = {
    domain: ['#F1803A']
  };

  constructor(private router: Router, private service: CustomSlideService, private checkoutService: CheckoutService) {
    this.BMI = this.calcBMI();
    this.userWeightKg = this.service.slideData.weight;
    this.userAge = this.service.slideData.age;
    this.gender = this.service.slideData.gender;
    this.idealWeight = this.calcIdealWeight();
  }

  ngOnInit() {
    this.pricing = this.checkoutService.getPricing();
    // redirect to home page, if any value is missing
    console.log(":: serviice", this.service.slideData);
    if (!this.service.allValuesPresent()) {
      this.router.navigate(['']);
    }
    this.single = [
      {
        "name": this.BMI,
        "value": this.BMI
      }
    ];
    this.customColors = (value) => {
      console.log(value,":: value");
      if (value < 18.5) {
        return "#FF0000";
      }
      if (value >= 18.5 && value < 25) {
        return "#008000";
      }
      if (value >= 25 && value < 30) {
        return "#FFA500";
      }
      if (value >= 30) {
        return "#FF0000";
      }
    }

    this.calcCalorie(this.service.slideData.goal, this.service.slideData.weight);
    this.singleCalorie = [
      {
        "name": "Calories",
        "value": this.calorie
      }
    ];
    
  }

  // calculating calorie on basis of goal
  calcCalorie(goal, weight){
    if(goal=="weightgain"){
      console.log(":: goal is weight gain");
      this.calorie = weight*42;
    }
    else if(goal=="weightloss"){
      console.log(":: goal is weight loss");
      this.calorie = weight*24;
    }
    else{
      console.log(":: goal is muscle")
      this.calorie = weight*32;
    }
  }




  onSelect(data): void {
    console.log('Item clicked', JSON.parse(JSON.stringify(data)));
  }

  onActivate(data): void {
    console.log('Activate', JSON.parse(JSON.stringify(data)));
  }

  onDeactivate(data): void {
    console.log('Deactivate', JSON.parse(JSON.stringify(data)));
  }

  handleCheckout() {
    this.router.navigate(["checkout"]);
  }

  calcBMI() {
    let userHeightFt = parseInt(this.service.slideData.height.split('ft')[0]);
    let userHeightIn = parseInt(this.service.slideData.height.split('ft')[1].split('inch')[0].trim());
    this.userHeightM = this.convertHeight(userHeightFt, userHeightIn);
    let weightKg = this.service.slideData.weight;
    return weightKg / (this.userHeightM * this.userHeightM);
  }

  convertHeight(userHeightFt, userHeightIn) {
    let userHeightMeter = (userHeightFt * 0.305) + (userHeightIn * 0.0254);
    return userHeightMeter;
  }

  calcIdealWeight() {
    let userHeightCm = this.userHeightM * 100;
    console.log(userHeightCm, "height in cm");
    console.log(this.gender, "gender");
    if (this.gender == "male") {
      const idealBodyWeight = userHeightCm - 100;
      console.log(idealBodyWeight, "maleBodyWeight");
      return idealBodyWeight;
    }
    else if (this.gender == "female") {
      const idealBodyWeight = userHeightCm - 105;
      console.log(idealBodyWeight, "femaleBodyWeight");
      return idealBodyWeight;
    }
  }

}
